<template>
  <div id="user-operation">
    <div>
      <i class="el-icon-user"></i>
      <span> {{ getUser() }}</span>
    </div>
    <div
      @click="modifyPawd">
      <i class="el-icon-lock"></i>
      <span> </span>
    </div>
    <div
      @click="initExit">
      <i class="el-icon-switch-button"></i>
      <span> </span>
    </div>
  </div>
</template>

<script>
import { delCookie, genericError } from '../func'
import { elconfirm, elmessage } from '../element-wrapper'
export default {
  methods: {
    modifyPawd () {
      this.$router.push('/memberService/pawdModify')
    },
    initExit () {
      const title = ''
      elconfirm(
        '', title,
        '', this.confirmExit,
        genericError
      )
    },
    confirmExit () {
      delCookie('token');
      // 
      this.global.memberInfo = {};
      this.global.isSighUpPage = false;
      this.global.formalMemberInfo = {};
      this.global.applyForms = [];
      this.global.editForm = {};
      this.global.payListData = [];
      this.$router.push('/login');
      elmessage('', 'warning');
    },
    getUser() {
      let username;
      try{
        username = this.global.memberInfo.username;
      } catch(e) {
        return ''
      }
      return username;
    }
  }
}
</script>

<style>
#user-operation {
  margin-left: 219px;
  padding-left: 20px;
  display: flex;
  height: 69px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  line-height: 69px;
}
#user-operation > div {
  margin-left: 30px;
}
#user-operation div:nth-child(2),
#user-operation div:nth-child(3) {
  cursor: pointer;
}
#user-operation div:nth-child(2):hover {
  color: #409EFF;
}
#user-operation div:nth-child(3):hover {
  color: #F56C6C;
}
</style>
